@use '~@/style/variables.scss' as *;

:local {
  .wrapper {
    height: calc(100% - 84px);
    overflow-y: auto;

    .header {
      height: 64px;
      padding: 20px 20px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .avatar {
        width: 40px;
        height: 40px;
        box-sizing: border-box;
        border: 1px solid $color-primary;
        border-radius: 20px;
        padding: 2px;

        :global(.adm-image) {
          height: 32px;
          width: 32px;
        }
      }
    }

    .title {
      text-align: center;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #91919f;

      & > p:last-child {
        font-weight: 600;
        font-size: 40px;
        line-height: 48px;
        color: #161719;
        padding-top: 9px;
      }
    }

    .sub-title {
      margin-top: 16px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 20px;

      > div {
        width: 164px;
        height: 80px;
        left: 16px;
        background: #00a86b;
        border-radius: 28px;
        display: inline-flex;
        justify-content: space-evenly;
        align-items: center;

        &:last-child {
          background: #fd3c4a;
        }

        > div {
          display: inline-block;

          > div {
            font-size: 14px;
            line-height: 17px;
            color: #fcfcfc;

            &:last-child {
              font-weight: 600;
              font-size: 22px;
              line-height: 27px;
            }
          }
        }
      }
    }

    .line-chart {
      p {
        height: 48px;
        font-weight: 600;
        font-size: 18px;
        line-height: 48px;
        color: #0d0e0f;
        margin-top: 12px;
        padding: 0 20px;
      }

      > div {
        height: 169px;
        border: 1px dashed rebeccapurple;
      }
    }

    .add-active {
      position: absolute;
      height: 100%;
      width: 100%;
      right: 0;
      left: 0;
      background: linear-gradient(
        180deg,
        rgb(139 80 255 / 12%) 0%,
        rgb(139 80 255 / 24%) 100%
      );
    }

    .tags {
      padding: 0 20px;

      > div {
        height: 34px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin-top: 8px;
      }
    }

    .content {
      padding: 0 20px;

      > div:first-child {
        height: 56px;
        font-weight: 600;
        font-size: 18px;
        color: #292b2d;
        display: flex;
        justify-content: space-between;
        align-items: center;

        div {
          width: 78px;
          height: 32px;
          background: #eee5ff;
          border-radius: 40px;
          display: inline-block;
          line-height: 32px;
          text-align: center;
          font-weight: 500;
          font-size: 14px;
          color: #7f3dff;
        }
      }

      > div:last-child {
        height: 270px;
        overflow-y: auto;
      }
    }
  }
}
